import React from "react";
import Axios from "axios";
class Register extends React.Component{
  constructor(){
  super();
  this.state={
   fname:"",
   lname:"",
   email:"",
   subject:"",
   message:""
  }
}
OnchangeHandler=(e)=>{
   this.setState({
     [e.target.name]:e.target.value

   });
 }
 OnSubmitHandler=(e)=>{
   const clientData=this.state;
   Axios.get('/',clientData)
     .then(function (response) {
       console.log(response);
     })
     .catch(function (error) {
       console.log(error);
     });
  }

 render(){
  return(
     <div className="container-fluid">

    <form id="contact-form" method="POST" action="/" onSubmit={this.OnSubmitHandler}>

        <div className="messages"></div>

        <div className="controls">

            <div className="row">
                <div className="col-md-6">
                    <div className="form-group">
                        <label htmlFor="form_name">Firstname *</label>
                        <input id="form_name" type="text" name="fname" className="form-control" placeholder="Please enter your firstname *" required="required" onChange={this.OnchangeHandler} data-error="Firstname is required."/>
                        <div className="help-block with-errors"></div>
                    </div>
                </div>
                <div className="col-md-6">
                    <div className="form-group">
                        <label htmlFor="form_lastname">Lastname *</label>
                        <input id="form_lastname" type="text" name="lname" className="form-control" placeholder="Please enter your lastname *" required="required" onChange={this.OnchangeHandler} data-error="Lastname is required."/>
                        <div className="help-block with-errors"></div>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-md-6">
                    <div className="form-group">
                        <label htmlFor="form_email">Email *</label>
                        <input id="form_email" type="email" name="email" className="form-control" placeholder="Please enter your email *" required="required" onChange={this.OnchangeHandler} data-error="Valid email is required."/>
                        <div className="help-block with-errors"></div>
                    </div>
                </div>
                <div className="col-md-6">
                    <div className="form-group">
                        <label htmlFor="form_subject">Please specify your subject *</label>
                        <input id="form_subject" type="text" name="subject" className="form-control" placeholder="Please enter your subject *" required="required" onChange={this.OnchangeHandler} data-error="Valid email is required."/>

                        <div className="help-block with-errors"></div>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-md-12">
                    <div className="form-group">
                        <label htmlFor="form_message">Message *</label>
                        <textarea id="form_message" name="message" className="form-control" placeholder="Message for me *" rows="4" required="required" data-error="Please, leave us a message." onChange={this.OnchangeHandler} ></textarea>
                        <div className="help-block with-errors"></div>
                    </div>
                </div>
                <div className="col-md-12">
                    <input type="submit" className="btn btn-success btn-send" value="Send message"/>
                </div>
            </div>
        </div>

    </form>
    </div>



);

 }
}
export default Register;
